footer h1 {
  font-size: 160%;
  font-weight: 500;
}

footer p {
  font-size: 120%;
}


/* Formatting Classes */

.med-img {
  width: 100%;
  height: auto;
}

.small-img {
  width: 50%;
  height: auto;
}

h1.intro{
  font-size: 220%;
  font-weight: 400;
  padding: 0px 10px;
}

h2.intro{
  font-size: 160%;
  font-weight: 400;
  padding: 10px;
}

h1.card{
  font-size: 180%;
  font-weight: 600;
  /* padding-left: 1rem; */
}

h2.card{
  font-size: 140%;
  font-weight: 400;
  margin-top: 4px;
}

.project h1 {
  padding-top: 80px;
}

.project h2 {
  color: #8E9AAF;
  font-weight: 600;
}

h1.small-card{
  font-size: 150%;
  font-weight: 500;
  padding: 0;
  margin: 0;
}

.project strong {
  font-weight: 800;
}

.project ul {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.project li {
  display: block;
  list-style-type: none;
  flex: 0 1 auto;
  /* float: left; */
  /* padding: 12px; */
}

.project li p {
  line-height: 0.5;
}





/* Formatting IDs */

#logo {
  font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-size: 28px;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: black;
}

#nav-links {
  text-align: right;
  float: right;
  padding: 10px 0px;
}

#nav-links ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  line-height: 2;
}

#nav-links li {
  display: inline;
}

#nav-links li a {
  font-size: 80%;
  font-weight: 500;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: #2F323B;
  padding: 12px;
}

#nav-links li a:hover {
  font-weight: 600;
  color: #bdc8db;
}

#social-icons a {
  color: #2F323B;
  margin-right: 24px;
}

#social-icons a:hover {
  color: #bdc8db;
}

#project-card {
  font-family: "Montserrat";
}

#project-card:hover {
  color: #2F323B;
}

#numbered-list h1 {
  padding-top: 0;
}

#takeaways h1 {
  margin: 0;
  padding-top: 50px;
}

#other-project h1 {
  font-size: 180%;
}


/* Media queries */
@media screen and (max-width: 480px) {
  h1 {
      font-size: 200%;
  }

  h2 {
      font-size: 150%;
  }

  #home-overview p {
      padding: 0px;
      font-size: 120%;
  }

  figcaption {
      font-size: 75%;
      padding: 0%;
  }


  .small-img {
      width: 100%;
      max-width: 100%;
      padding: 10px 0px;
  }

  #aboutme img, #aboutme p {
      padding: 15px 0px;
  }

}




